#user_img{
    width: 12%; 
    cursor: pointer; 
    clip-path: circle();
}

#user_name{
    width: fit-content;
}

#user_id{
    padding-left: 10px;
    font-weight: 400;
    color: rgb(104, 104, 104);
}

#user_id:hover{
    cursor: pointer;
}

.user_description > MuiSvgIcon{
    transition: scale(1.2);
}

.user_description{
    display: flex;
    align-items: start;
}

.blue_tick{
    color: rgb(30, 139, 255);
}

.blue_tick > .MuiSvgIcon-root{
    font-size: 23px !important;
}

#user_post{
    height: 525px;
    width: 525px;
}

#user_post{
    border-radius: 20px;
}

.post__footer{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.post__footer:hover{
    cursor:pointer;
}

.pf1:hover{
    color: rgb(89, 127, 242);
}

.pf2:hover{
    color: rgb(255, 111, 212);
}

.pf3:hover{
    color: rgb(197, 69, 252);
}

.pf4:hover{
    color: rgb(43, 57, 252);
}

.post_content{
    justify-content: center;
}