.sidebarOption{
    display: flex;
    align-items:center;
    cursor:pointer;
    padding:5px; 
}

.sidebarOption:hover{
    background-color: rgb(233, 233, 233);
    border-radius: 100px;
    /* color: rgb(207, 245, 253); --> it changes the color of the texts, but achha nhi lg rha tha*/ 
    transition: background-color 0.1s;
}

.sidebarOption > .MuiSvgIcon-root{      /* "MuiSvgIcon-root" ek class h..iss class k andar humare Special Vector Graphics (SVG/icons) maujood h. Isiliye icons k through padding badani pdi*/
    padding:10px;
}

#text_for_icons{
    font-weight: 400;
    font-size: medium;
}

.sidebarOption--active{
    color: rgb(30, 139, 255);
}
