.sidebar{
    flex:0.25;
}

#Main_Twitter_Icon{
    transform: scale(1.5);
    padding-top: 25px;
    padding-left: 60px;
    padding-bottom: 20px;
    color: rgb(30, 139, 255);
}

.sidebar__tweetButton{
    background-color: rgb(30, 139, 255) !important; /* tumne "material icon" website se poora button ka module uthaya h....vahaa se button ki already defined kuchh propertes aayi h....now by the use of "!important" you are telling the IDE that the properties which I'm giving are important, toh usko execute kro */
    border:none !important;
    color:white !important;
    font-size: medium !important;
    border-radius: 50px !important;
    height: 45px !important;
    margin-top: 20px !important;
    width: 200px !important;
    text-transform: inherit !important;
}

.sidebar__tweetButton:hover{
    background-color: rgb(255, 255, 255) !important;
    color: rgb(30, 139, 255)!important;
    border: 2px solid rgb(30, 139, 255)!important;   
}